}
#search input {
    background: #FFF;
    padding: 1px 50px;
    width: 100%;
    height: 40px;
    border: 1px solid #EEEEEE;
    position:relative;
}
.sub {
    width:40px;
    height:40px;
    position:absolute;
    right:0;
    top:0;
    text-align:center;
    border-left:1px solid #ccc;
