}
.wrap-header .top {
  padding: 10px;
  height:60px;
}
.button-search {
    position: absolute;
    left: 0px;
    background: url(data:image/png;base64,) center center no-repeat;
    width: 40px;
    height: 40px;
    border-right: 1px solid #EEEEEE;
    cursor: pointer;
    z-index:2;
}
.wrap-header #logo {
    background: #76D2C5;
    width: 100%;
    text-align: center;
    padding: 40px 0px;
    color: #ffffff;
}
.wrap-header #logo h1 {
    font-size: 60px;
    line-height: 80px;
    color: #ffffff;
}
nav .wrap-nav {
    display: inline-block;
    background: #91DBD1;
    width: 100%;
}
.menu ul {
    cursor: pointer;
    font-size:14px;
}
.menu ul li {
    width: 20%;
    float: left;
    text-align: center;  
    height: 50px;
    line-height:25px;
}
.menu ul li:hover {
  background-color: #76F2C5;
}
.menu ul li:hover a {
  color: #000;
}
.menu ul li a {
    display: block;
    width: 100%;
    height: 100%;
    padding: 15px 0;
    color:#fff;
}
@media screen and (min-width:320px) {
