/* 初始化1：去掉默认的边距 */
body,div,span,ul,ol,li,p,h1,h2,h3,h4,h5,h6,dl,dt,dd,form,input,img,a,select,textarea,button,header,aside,article,footer {
	padding:0;
	margin:0;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
/* 初始化2：清除列表前的默认的点或序号 */
ul,ol {
	list-style: none;
}
/* 初始化3：清除默认的边框 */
input {
	border:none;
	outline:none;
}
/* 初始化4：给页面设立一个统一的字体大小、类型、行高，以及body的背景色 */
body {
	font:normal normal 12px/12px "Microsoft","\5B8B\4F53"，tahoma, arial, "Hiragino Sans GB", "\\5b8b\4F53", sans-serif;
	background-color: #fff;
}
/* 初始化5： 设置a标签的字体颜色以及去掉下划线 */
a {
	color:#000;
	text-decoration: none;
}
/* 初始化6:清除浮动的样式代码 */
.clearfix:after {
	content:"";
	display: block;
	clear:both;
	height: 0;
	line-height: 0;
	visibility:hidden;
}
.clearfloat {
	zoom:1;   /*兼容ie低版本浏览器  */
}
/* 初始化7：设置浮动 */
.fl {
	float:left;
}
.fr {
	float:right;
}
.menu ul li a.navactive {
	color:#000;
	background-color: #fff;
}

.in_theaters .ul1 {
    width:950px;
    margin:0 auto;
}
.in_theaters .ul1 li {
    float:left;
    padding:20px;
    line-height: 25px;
}
.in_theaters .ul1 li a {
    display:block;
}
.in_theaters .ul1 li a img {
    display:block;
    width:150px;
    height:200px;
}
.in_theaters .ul1 li p {
    padding: 20px 0 0 0;
    text-align: center;
    font-weight: bold;
    font-size: 15px;
    height:50px;
}
.in_theaters .pageIndicator li {
    float:left;
    width:50px;
    height:50px;
    border:1px solid #91DBD1;
    text-align:center;
    line-height:50px;
}
.in_theaters .pageIndicator {
    position:absolute;
    left:50%;
    top:0;
    -webkit-transform:translate(-50%,0);
    transform:translate(-50%,0);
    cursor:pointer;
}
.indicator_container {
    width:100%;
    height:100px;
    position:relative;
}
.in_theaters .pageIndicator li.num+li.num {
    margin-left:5px;
}
.in_theaters .pageIndicator li:nth-of-type(1) {
    border-radius: 50%;
    font-size:14px;
    background-color:#91DBD1;
    margin-right:20px;
    -webkit-user-select:none;
    -moz-user-select:none;
    -ms-user-select:none;
    user-select:none;
}
.in_theaters .pageIndicator li:last-of-type {
    border-radius: 50%;
    font-size:14px;
    margin-left:20px;
    background-color:#91DBD1;
    -webkit-user-select:none;
    -moz-user-select:none;
    -ms-user-select:none;
    user-select:none;
}
.indicatorActive {
    background-color:#91DBD1;
}
@media screen and (min-width:320px) {
    .in_theaters .ul1 li a img {
        width: 100px;
        height: 150px;
    }
    .in_theaters .ul1 {
        width:280px;
    }
    .in_theaters .ul1 p {
        width:100px;
    }
    .in_theaters .pageIndicator li {
        width:25px;
        height:25px;
        line-height:25px;
    }
}
@media screen and (min-width:400px) {
    .in_theaters .ul1 {
        width:390px;
    }
    .in_theaters .ul1 li a img {
        width: 150px;
        height: 200px;
    }
    .in_theaters .ul1 li p {
        width:150px;
    }
}
@media screen and (min-width:590px) {
    .in_theaters .ul1 {
        width:580px;
    }
}
@media screen and (min-width:780px) {
    .in_theaters .ul1 {
        width:770px;
    }
}
@media screen and (min-width:960px) {
    .in_theaters .ul1 {
        width:950px;
    }
    .in_theaters .pageIndicator li {
        width:50px;
        height:50px;
        line-height:50px;
    }
}